import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Autoplay, Pagination, Navigation, Keyboard } from "swiper/modules";
import * as styles from "./CardStackSlider.module.css";

const CardStackSlider = ({ list }) => {
    const [activeVideo, setActiveVideo] = useState(null);
    const [swiperInstance, setSwiperInstance] = useState(null); // Store Swiper instance

    const handleThumbnailClick = (videoId) => {
        setActiveVideo(videoId); // Set the clicked video as active
        if (swiperInstance?.autoplay) {
            swiperInstance.autoplay.stop(); // Stop autoplay safely
        }
    };

    return (
        <Swiper
            onSwiper={setSwiperInstance} // Capture Swiper instance
            spaceBetween={30}
            centeredSlides={true}
            autoplay={{
                delay: 1300,
                disableOnInteraction: false,
            }}
            pagination={{
                clickable: true,
            }}
            navigation={true}
            keyboard={{
                enabled: true, // Enable keyboard navigation
                onlyInViewport: true, // Activate keyboard control only when Swiper is visible
            }}
            modules={[Autoplay, Pagination, Navigation, Keyboard]}
            className={styles.swiperContainer} // Apply CSS class
        >
            {list.map((video) => (
                <SwiperSlide key={video.id}>
                    <div className={styles.swiperSlideWrapper}>
                        {activeVideo === video.id ? (
                            <iframe
                                src={`https://www.youtube.com/embed/${video.id}?controls=1&autoplay=1&modestbranding=1&rel=0`}
                                title={`Video - ${video.title}`}
                                frameBorder="0"
                                allow="autoplay; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen
                                className={`${styles.sharedStyles} ${styles.videoIframe}`} // Apply classes
                            ></iframe>
                        ) : (
                            <img
                                src={`https://img.youtube.com/vi/${video.id}/hqdefault.jpg`}
                                alt={`Thumbnail for ${video.title}`}
                                className={`${styles.sharedStyles} ${styles.videoThumbnail}`} // Apply classes
                                onClick={() => handleThumbnailClick(video.id)}
                            />
                        )}
                    </div>
                </SwiperSlide>
            ))}
        </Swiper>
    );
};

export default CardStackSlider;
